import React, { useState } from 'react';
import Lottie from 'react-lottie';
import styled from 'styled-components';
import animationData from '../lotties/home_signal.json';

const ForyouContainer = styled.div`
	position:relative;
	h3{
		font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: -.35px;
    color: #787d86;
		margin: 0 16px 24px;
	}
`;

const StickyHeader = styled.div`
	margin: 40px 16px 16px 15px;
	display: flex;
  align-items: center;
  background-color: #fff;
  position: sticky;
  top: 64px;
  z-index: 2;
  height: 40px;
	h1{
		display: flex;
    align-items: center;
    width: 681px;
    height: 40px;
    font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    font-weight: 700;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.2;
    font-size: 32px;
    letter-spacing: -.8px;
    color: #272b31;
    z-index: 2;
		.dropdown-container{
			position: relative;
    	height: 32px;
			.info-button{
				margin-left: 9px;
				width: 32px;
    		height: 32px;
    		background-size: 20px;
    		background-repeat: no-repeat;
    		background-position: 50%;
    		background-image: url('https://getliner.com/src/images/info.svg');
    		border-radius: 50%;
    		z-index: 7;
    		position: absolute;
				&:hover{
					background-color:#f6f8fa;
				}
			}
			.guide-tooltip-container{
				top: -24px;
    		margin-left: 60px;
				z-index: 50;
    		width: 329px;
    		height: 280px;
    		position: absolute;
    		display: flex;
				.tooltip-triangle{
					width: 19px;
    			height: 19px;
    			background-color: #52565d;
    			position: absolute;
    			top: 30px;
    			left: -9px;
    			transform: rotate(45deg);
    			z-index: 2;
    			border-bottom-left-radius: 4px;
    			border-bottom: 1px solid #969aa2;
    			border-left: 1px solid #969aa2;
				}
				.tooltip-rectangle{
					justify-self: flex-end;
    			width: 320px;
    			height: 280px;
    			box-shadow: 0 8px 20px 0 rgb(39 43 49 / 20%);
    			border: 1px solid #969aa2;
    			background-color: #52565d;
    			border-radius: 8px;
    			z-index: 1;
    			position: relative;
				}
				.close-btn{
					background-image: url('https://getliner.com/src/images/close-be-comment.svg');
    			background-repeat: no-repeat;
    			background-size: 24px 24px;
    			width: 24px;
    			height: 24px;
    			position: absolute;
    			right: 8px;
    			top: 8px;
    			z-index: 3;
				}
				.lottie-container{
					width: 100%;
    			height: 160px;
    			border-top-left-radius: 7px;
    			border-top-right-radius: 7px;
				}
				.text-container{
					margin-top: -10px;
    			height: 40%;
    			display: flex;
    			flex-direction: column;
    			justify-content: center;
    			padding: 15px 17px;
					text-align: left;
					.text-title{
						font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    				font-weight: 700;
    				font-stretch: normal;
    				font-style: normal;
    				line-height: normal;
    				font-size: 16px;
    				letter-spacing: -.4px;
    				color: #fff;
					}
					.text-contents{
						margin-top: 8px;
    				font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    				font-weight: 400;
    				font-stretch: normal;
    				font-style: normal;
    				line-height: 20px;
    				font-size: 14px;
    				letter-spacing: -.35px;
    				color: #fff;
					}
				}
			}
		}
		.language{
			justify-self: flex-end;
    	margin-left: auto;
    	display: flex;
    	align-items: center;
    	align-self: flex-end;
			position: relative;
    	height: 32px;
			.language-btn{
				display: flex;
    		align-items: center;
    		align-self: flex-end;
    		font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    		font-size: 14px;
    		font-weight: 400;
    		font-stretch: normal;
    		font-style: normal;
    		line-height: normal;
    		letter-spacing: -.35px;
    		color: #787d86 !important;
				.expand{
					background-image: url('https://getliner.com/src/images/expand-grey.svg');
    			width: 16px;
    			height: 16px;
    			object-fit: contain;
    			margin-left: 4px;
				}
				.collapse{
					background-image: url('https://getliner.com/src/images/collapse.svg');
    			width: 16px;
    			height: 16px;
    			object-fit: contain;
    			margin-left: 4px;
				}
			}
			.language-dropdown{
				position: relative;
    		left: -200px;
    		top: -15px;
    		z-index: 3!important;
				.dropdown{
					position: absolute;
    			top: 34px;
    			min-width: 200px;
    			width: max-content;
    			white-space: nowrap;
    			z-index: 2;
    			border-radius: 8px;
    			box-shadow: 0 10px 20px 0 rgb(39 43 49 / 10%);
    			border: 1px solid #eef1f4;
    			background-color: #fff;
    			padding: 8px 0;
    			margin: 0;
					.dropdown-header{
						height: 36px;
    				font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    				font-size: 14px;
    				font-weight: 700;
    				font-stretch: normal;
    				font-style: normal;
    				line-height: normal;
    				letter-spacing: -.35px;
    				color: #969aa2;
    				display: flex;
    				padding: 8px 15px 0;
					}
					.selected{
						font-weight: 500 !important;
						color: #00bdb8 !important;
						display: flex !important;
					}
					.check{
						background-image: url('https://getliner.com/src/images/check-primary.svg');
    				background-repeat: no-repeat;
    				background-size: 14px 10px;
    				width: 14px;
    				height: 10px;
    				justify-self: flex-end;
    				margin-left: auto;
					}
					.language-li{
						list-style-type: none;
    				height: 40px;
    				background-color: #fff;
    				font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    				font-size: 14px;
    				font-weight: 400;
    				font-stretch: normal;
    				font-style: normal;
    				line-height: normal;
    				letter-spacing: -.35px;
    				color: #52565d;
    				padding: 8px 15px;
    				display: flex;
    				align-items: center;
    				cursor: pointer;
						&:hover{
							background-color: #f6f8fa;
						}
					}
				}
			}
		}
	}
`;

const lottieSetting = {
	loop: true,
	autoplay: true,
	animationData : animationData,
	rendererSettings: {
		preserveAspectRatio : 'xMidYMid slice'
	}
};

const PageList = styled.div`
	.page-container{
		padding: 24px 16px 0;
    width: 712px;
    position: relative;
		.page-contents-container{
			display: flex;
    	justify-content: space-between;
    	padding-bottom: 16px;
    	overflow-x: hidden;
		}
		.page-footer{
			height: 48px;
    	display: flex;
    	justify-content: space-between;
    	width: 680px;
    	padding-bottom: 16px;
		}
		.page-list-separator{
			height: 8px;
    	border-bottom: 1px solid #eef1f4;
		}
	}
`;

function PageArticle() {
	return(
		<article className="page-container">
			<div className="page-contents-container"></div>
			<div className="page-footer"></div>
			<div className="page-list-separator"></div>
		</article>
	);
}

function ForYou() {
	const [lottie, setLottie] = useState(false);
	const [pause, setPause] = useState(false);
	const [languageModal, setlanguageModal] = useState(false);
	const [languages, setLanguages] = useState([
		{
			lang: 'English',
			id: 1,
			done: false
		},
		{
			lang: '한국어',
			id: 2,
			done: false
		},
		{
			lang: '简体字',
			id: 3,
			done: false
		},
		{
			lang: '繁體字',
			id: 4,
			done: false
		},
		{
			lang: '日本語',
			id: 5,
			done: false
		},
		{
			lang: 'Others',
			id: 6,
			done: false
		},
	])

	const onClick = () => {
		setLottie(prev=> !prev)
	}
	const onPause = () =>{
		setPause(prev => !prev)
	}
	const onToggle = (event) => {
		const {target:{id}} = event;
		setLanguages(languages.map(lang => 
				lang.id === parseInt(id) ? {...lang, done : !lang.done} : lang
		))
		console.log(languages)
	}

	const openModal = () => {
		setlanguageModal(prev => !prev)
	}

	return (
		<ForyouContainer>
			<StickyHeader>
				<h1>
					For You
					<div className="dropdown-container">
						<button className="info-button home" onClick={onClick}></button>
						{lottie && (
						<div className="guide-tooltip-container" id="for_you">
							<div className="tooltip-triangle" id="for_you"></div>
							<div className="tooltip-rectangle">
								<button className="close-btn" onClick={onClick}></button>
								<div className="lottie-container">
									<Lottie 
										options={lottieSetting}
										isPaused={pause}
										onClick={onPause}
										height={160}
										width={320}
									/>
								</div>
								<div className="text-container" id="for_you">
									<div className="text-title">For You</div>
									<div className="text-contents">
										We recommend you pages you'll love and need. The more you highlight, the smarter our algorithm gets.
									</div>
								</div>
							</div>
						</div>
						)}
					</div>
					<div className="language false dropdwon-container">
						<button className="language-btn false" onClick = {openModal}>
							Languages
							<div className={languageModal ? 'collapse': 'expand'}></div>
						</button>
						{languageModal && <div>
							<div className="language-dropdown">
								<ul className="dropdown">
									<div className="dropdown-header">Select for feed</div>
									{languages.map(lang => (
										<li onClick={onToggle}className={lang.done ? "language-li selected" : "language-li notselected"} key={lang.id} id={lang.id}>
											<span >{lang.lang}</span>
											{lang.done && <div className="check"></div>}
										</li>
									))}
								</ul>
							</div>
						</div>}
					</div>
				</h1>
			</StickyHeader>
			<h3>Picked by LINER, just for you</h3>
			<PageList>
				<PageArticle/>
			</PageList>
		</ForyouContainer>
	);
}

export default ForYou;